'use strict'

import { strict as assert } from 'assert'
import { unit, createUnit } from 'mathjs'
import proj4 from 'proj4'

import { USFT_TO_METERS, USYD_TO_METERS } from './constants'
import * as position from './position'

class UnitConverter {
  /**
   *
   * @param {object} params
   * @param {object} params.outputMeasurementSystem The output measurementSystem to be used
   * @param {object} [params.inputMeasurementSystem] The input measurementSystem to be used
   * @param {object} [params.matrix] The transform matrix. If not set `parameters` must be set
   * @param {object} [params.parameters] The transform parameters. If not set `matrix` must be set
   * @param {object} [params.geoidHeight] The geoid height to apply. Only used when transforming using parameters.
   */
  constructor (params = {}) {
    Object.assign(
      this,
      {
        inputMeasurementSystem: UnitConverter.defaultInputMeasurementSystem,
        inputZRef: 'ellipsoid', // ignored when using matrix transformation
        outputZRef: 'geoid', // ignored when using matrix transformation
        geoidHeight: 0
      },
      params
    )

    // map this.transform to this.matrix
    if (this.transform && !this.matrix) {
      this.matrix = this.transform
    }

    assert.equal(typeof this.inputMeasurementSystem, 'object')
    assert.equal(typeof this.outputMeasurementSystem, 'object')
    assert.ok(
      ['ellipsoid', 'geoid'].indexOf(this.inputZRef) !== -1,
      'inputZRef must be oneof "ellipsoid" or "geoid"'
    )
    assert.ok(
      ['ellipsoid', 'geoid'].indexOf(this.outputZRef) !== -1,
      'outputZRef must be oneof "ellipsoid" or "geoid"'
    )

    // expose unit for testing
    this.unit = unit
  }

  static get defaultInputMeasurementSystem () {
    return {
      position: 'dd',
      length: 'm',
      area: 'm2',
      volume: 'm3',
      mass: 'tonne',
      density: 'tonne/m3'
    }
  }

  reverse () {
    const params = Object.assign({}, this, {
      inputMeasurementSystem: this.outputMeasurementSystem,
      outputMeasurementSystem: this.inputMeasurementSystem,
      inputZRef: this.outputZRef,
      outputZRef: this.inputZRef
    })
    return new UnitConverter(params)
  }

  position (x, y, z) {
    const { matrix, parameters } = this
    assert.equal(
      !!matrix || !!parameters,
      true,
      'To convert a position you must specify either a transform matrix or parameters'
    )

    // matrix if present takes precendence over parameters and geoidHeight
    if (matrix && Object.keys(matrix).length > 0) {
      return this._applyMatrixToPoint(...arguments)
    }
    return this._applyParametersAndGeoidToPoint(...arguments)
  }

  _applyParametersAndGeoidToPoint (x, y, z) {
    assert.equal(
      typeof this.parameters,
      'object',
      'Missing transform parameters'
    )

    let xfinal, yfinal
    const {
      inputMeasurementSystem,
      outputMeasurementSystem,
      parameters,
      geoidHeight = 0,
      inputZRef,
      outputZRef
    } = this

    const inputXYUnit = this._cleanUnit(inputMeasurementSystem.position)
    const inputZUnit = this._cleanUnit(inputMeasurementSystem.length)
    const outputXYUnit = this._cleanUnit(outputMeasurementSystem.position)
    const outputZUnit = this._cleanUnit(outputMeasurementSystem.length)
    const { value: xraw } = this._parseValue(x, inputXYUnit)
    const { value: yraw } = this._parseValue(y, inputXYUnit)
    const { value: zraw, unit: zunit } = this._parseValue(z, inputZUnit)

    if (inputXYUnit === outputXYUnit) {
      xfinal = unit(xraw, outputXYUnit).toNumeric()
      yfinal = unit(yraw, outputXYUnit).toNumeric()
    } else if (inputXYUnit === 'dd' && outputXYUnit !== 'dd') {
      // Convert from WGS84 -> LOCAL

      const lon = xraw
      const lat = yraw

      // Convert from WGS84 to the specified projection
      const inproj = 'EPSG:4326'
      const outproj = parameters.projection.proj4
      const coord = proj4(inproj, outproj).forward([lon, lat])
      xfinal = coord[0]
      yfinal = coord[1]
    } else if (inputXYUnit !== 'dd' && outputXYUnit === 'dd') {
      // Convert from LOCAL => WGS84

      // 1. convert from WGS84 to the specified projection and apply the geoid height
      const inproj = parameters.projection.proj4
      const outproj = 'EPSG:4326'
      const coord = proj4(inproj, outproj).forward([xraw, yraw])
      xfinal = coord[0]
      yfinal = coord[1]
    }

    // Adjust the Z

    // z value should be in meters before applying the geoid height
    const zmeter = unit(zraw, zunit)
      .to('m')
      .toNumeric()

    // Apply the geoid height (to get the ellipsoid/orthometric height)
    // This value is always in meters
    const zfinalm = this._applyGeoidHeight(
      zmeter,
      geoidHeight,
      inputZRef,
      outputZRef
    )

    // Convert zfinalm to output unit
    const zfinal = unit(zfinalm, 'm')
      .to(outputZUnit)
      .toNumeric()

    return {
      x: unit(xfinal, outputXYUnit),
      y: unit(yfinal, outputXYUnit),
      z: unit(zfinal, outputZUnit)
    }
  }

  _applyMatrixToPoint (x, y, z) {
    assert.equal(typeof this.matrix, 'object', 'Missing transform matrix')

    let xfinal, yfinal, zfinal
    const { inputMeasurementSystem, outputMeasurementSystem, matrix } = this
    const { utmZone } = matrix

    const inputXYUnit = this._cleanUnit(inputMeasurementSystem.position)
    const inputZUnit = this._cleanUnit(inputMeasurementSystem.length)
    const outputXYUnit = this._cleanUnit(outputMeasurementSystem.position)
    const outputZUnit = this._cleanUnit(outputMeasurementSystem.length)
    const { value: xraw } = this._parseValue(x, inputXYUnit)
    const { value: yraw } = this._parseValue(y, inputXYUnit)
    const { value: zraw, unit: zunit } = this._parseValue(z, inputZUnit)

    if (inputXYUnit === outputXYUnit) {
      xfinal = unit(xraw, outputXYUnit).toNumeric()
      yfinal = unit(yraw, outputXYUnit).toNumeric()
      zfinal = unit(zraw, zunit)
        .to(outputZUnit)
        .toNumeric()
    } else if (inputXYUnit === 'dd' && outputXYUnit !== 'dd') {
      // Convert from WGS84 -> LOCAL

      // 1. z value should be in meters (and assumed to be already height above ellipsoid)
      const zmeter = unit(zraw, zunit)
        .to('m')
        .toNumeric()
      const lon = xraw
      const lat = yraw

      // 2. convert from WGS84 to UTM
      const inproj = 'EPSG:4326'
      const outproj = position.getProj4FromZone(utmZone)
      const coord = proj4(inproj, outproj).forward([lon, lat])
      const xutm = coord[0]
      const yutm = coord[1]

      // 3. convert from UTM to LOCAL
      const { utm2local } = matrix
      const res = position.transformPoint(utm2local, [xutm, yutm, zmeter])
      xfinal = res.x
      yfinal = res.y
      zfinal = res.z
    } else if (inputXYUnit !== 'dd' && outputXYUnit === 'dd') {
      // Convert from LOCAL -> WGS84

      // 1. convert from LOCAL to UTM
      const { local2utm } = matrix
      const { x: xutm, y: yutm, z: zmeter } = position.transformPoint(
        local2utm,
        [xraw, yraw, zraw]
      )

      // 2. convert from UTM to WGS84
      // const inproj = position.getProj4FromCoords(lat, lon)
      const inproj = position.getProj4FromZone(utmZone)
      const outproj = 'EPSG:4326'
      const coord = proj4(inproj, outproj).forward([xutm, yutm])
      xfinal = coord[0]
      yfinal = coord[1]

      // 3. convert z to output unit
      zfinal = unit(zmeter, 'm')
        .to(outputZUnit)
        .toNumeric()
    }

    return {
      x: unit(xfinal, outputXYUnit),
      y: unit(yfinal, outputXYUnit),
      z: unit(zfinal, outputZUnit)
    }
  }

  value (quantity, value) {
    const input = this._wrapInput(quantity, value)
    let targetUnit = this.outputMeasurementSystem[quantity]
    targetUnit = this._cleanUnit(targetUnit)
    return input.to(targetUnit)
  }

  _wrapInput (quantity, value) {
    const parsed = this._parseValue(
      value,
      this.inputMeasurementSystem[quantity]
    )
    const inputUnit = parsed.unit
    value = parsed.value
    return unit(value, inputUnit)
  }

  _parseValue (value, defaultUnit) {
    let unit = defaultUnit
    // check to see if we are dealing with a string that contains units
    if (typeof value === 'string') {
      const parts = value.split(' ')
      value = parseFloat(parts.shift()) // first part will always be the value

      // this check allows us to support string value with no unit
      if (parts.length) {
        unit = parts.join('') // remaining part is the unit
      }
    }
    unit = this._cleanUnit(unit)
    return { value, unit }
  }

  _cleanUnit (unit) {
    // mathjs unit must contain alphanumeric or / only
    return unit.replace(/[-_]/gi, '')
  }

  _applyGeoidHeight (zmeter, geoidHeight, inputZRef, outputZRef) {
    if (inputZRef === 'ellipsoid' && outputZRef === 'geoid') {
      // Compute the orthometric height
      // The formula is H = h - N, where H is orthometric height, h is ellipsoid height, and N is geoid height
      return zmeter - geoidHeight // return the orthometric height
    } else if (inputZRef === 'geoid' && outputZRef === 'ellipsoid') {
      // Compute the ellipsoid height
      // The formula is h = H + N, where H is orthometric height, h is ellipsoid height, and N is geoid height
      return zmeter + geoidHeight // return the ellipsoid height
    }
    // inputZRef and outputZRef are the same so do nothing
    return zmeter
  }
}

// setup convenience methods for simple unit transformation
;['length', 'area', 'volume', 'mass', 'density'].forEach(method => {
  UnitConverter.prototype[method] = function () {
    return this.value(method, ...arguments)
  }
})

// setup new units ///////////////////////////////////////

createUnit('dd')
createUnit('usft', {
  definition: `${USFT_TO_METERS} m`,
  aliases: ['ftus']
})
createUnit('usft2', {
  definition: `${Math.pow(USFT_TO_METERS, 2)} m2`,
  aliases: ['ussqft', 'sqftus']
})
createUnit('usft3', {
  definition: `${Math.pow(USFT_TO_METERS, 3)} m3`,
  aliases: ['uscuft', 'cuftus']
})
createUnit('usyd', {
  definition: `${USYD_TO_METERS} m`,
  aliases: ['ydus']
})
createUnit('usyd2', {
  definition: `${Math.pow(USYD_TO_METERS, 2)} m2`,
  aliases: ['ussqyd', 'sqydus']
})
createUnit('usyd3', {
  definition: `${Math.pow(USYD_TO_METERS, 3)} m3`,
  aliases: ['uscuyd', 'cuydus']
})

export default UnitConverter
