'use strict'

const dmsToDeg = (deg, min, sec) => {
  if (deg && min && sec) {
    return deg + min / 60 + sec / 3600
  } else {
    var parts = deg.split(' ').map(function (n) {
      return parseFloat(n)
    })
    return parts[0] + parts[1] / 60 + parts[2] / 3600
  }
}

/**
 * transformPoint - Apply tranformation matrix to a point
 *
 * @param {array} matrix the transformation matrix
 * @param {array} point  [x, y, z]
 * @return {array} [x, y, z]
 */
const transformPoint = (matrix, point) => {
  const x =
    matrix[0][0] * point[0] +
    matrix[0][1] * point[1] +
    matrix[0][2] * point[2] +
    matrix[0][3] * 1
  const y =
    matrix[1][0] * point[0] +
    matrix[1][1] * point[1] +
    matrix[1][2] * point[2] +
    matrix[1][3] * 1
  const z =
    matrix[2][0] * point[0] +
    matrix[2][1] * point[1] +
    matrix[2][2] * point[2] +
    matrix[2][3] * 1
  return { x, y, z }
}

const getZone = (lat, lon) => {
  if (!(lat >= -80 && lat <= 84)) throw new Error('Outside UTM limits')

  const zone = Math.floor((lon + 180) / 6) + 1 // longitudinal zone

  if (lat < 0) return `${zone}S`
  return `${zone}N`
}

const getProj4FromZone = zone => {
  const isSouth = zone.substr(-1, 1).toLowerCase() === 's'

  if (zone < 1 || zone > 60) {
    throw new Error('UTM zone invalid')
  }

  if (isSouth) {
    return `+proj=utm +zone=${zone} +south +ellps=WGS84 +datum=WGS84 +units=m +no_defs`
  } else {
    return `+proj=utm +zone=${zone} +ellps=WGS84 +datum=WGS84 +units=m +no_defs`
  }
}

/**
 * Returns the WGS84 UTM Zone EPSG code for a give latlon
 * @see http://gis.stackexchange.com/questions/33219/how-to-convert-lat-long-to-utm-using-proj4j-similar-to-jscience-utm-latlongtou
 */
const getEPSGCodeFromCoords = (lat, lon) => {
  const zone = getZone(lat, lon)
  const zoneNum = +zone.replace('N', '').replace('S', '')

  if (zone.indexOf('S') !== -1) {
    return `EPSG:${32700 + zoneNum}`
  }
  return `EPSG:${32600 + zoneNum}`
}

const getEPSGCodeFromZone = zone => {
  const zoneNum = +zone.replace('N', '').replace('S', '')
  if (zone.indexOf('S') !== -1) {
    return `EPSG:${32700 + zoneNum}`
  }
  return `EPSG:${32600 + zoneNum}`
}

const getProj4FromCoords = (lat, lon) => {
  const zone = getZone(lat, lon)
  return getProj4FromZone(zone)
}

export {
  getEPSGCodeFromCoords,
  getEPSGCodeFromZone,
  getProj4FromCoords,
  getProj4FromZone,
  getZone,
  transformPoint,
  dmsToDeg
}
